@import "../../styles/utils";

.footer {
  margin-top: 88px;
  height: 359px;
  position: sticky;
  background-image: url("../../images/footer.jpg");
  background-size: cover;
  background-position: center;
  
  @media screen and (max-width: 550px) {
    & {
      height: 206px;
    }
  }
  
  &__logo {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 40px;
  }
}
