@import "../../styles/utils";

.testimonials {
  text-align: center;
  
  &__container {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 1050px) {
      & {
        justify-content: space-evenly;
      }
    }

    @media screen and (max-width: 780px) {
      & {
        justify-content: space-around;
        flex-wrap: wrap;
      }
    }
  }
  
  &__article {
    width: 197px;
    
    @media screen and (max-width: 780px) {
      & {
        margin: 10px 15px;
      }
    }
  }
}
